import React from 'react';
import './TodoItem.css';

export const TodoItme = ({todo, onDelete, onToggle}) => {
  const handleDeleteClick = (e) => {
    e.stopPropagation(); // Prevent the click event from bubbling up to the parent
    onDelete(todo);
  };

  const handleToggleClick = (e) => {
    e.stopPropagation();
    onToggle(todo);
  };

  return (
    <div className="todo-item" onClick={handleToggleClick}>
    <div className={`todo-check ${todo.completed? 'checked' : ''}`} onClick={handleToggleClick}></div>
    <span className={`todo-title ${todo.completed? 'completed' : ''}`}>{todo.title}</span>
    <span className="delete-btn" onClick={handleDeleteClick}>&#x2715;</span>
  </div>
  );
};
export default TodoItme;
