// src/components/Sidebar.js

import React from 'react';
import { NavLink } from 'react-router-dom';
import './Sidebar.css';

export const Sidebar = () => {
  return (
    <nav className="sidebar">
      <ul>
        <li>
          <NavLink exact to="/" activeClassName="active">
            <i className="fas fa-calendar-day"></i> Today's Tasks
          </NavLink>
        </li>
        <li>
          <NavLink to="/weekly" activeClassName="active">
            <i className="fas fa-calendar-week"></i> Weekly Tasks
          </NavLink>
        </li>
        <li>
          <NavLink to="/monthly" activeClassName="active">
            <i className="fas fa-calendar-alt"></i> Monthly Tasks
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Sidebar;
