import React, { useState, useEffect } from 'react';
import './App.css';
import Header from "./components/Header";
import { Sidebar } from './components/Sidebar';
import { TodayTasks } from './components/TodayTasks';
import { WeeklyTasks } from './components/WeeklyTasks';
import { MonthlyTasks } from './components/MonthlyTasks';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  const [todayTodos, setTodayTodos] = useState([]);
  const [weeklyTodos, setWeeklyTodos] = useState([]);
  const [monthlyTodos, setMonthlyTodos] = useState([]);

  useEffect(() => {
    const storedTodayTodos = JSON.parse(localStorage.getItem("todayTodos")) || [];
    const storedWeeklyTodos = JSON.parse(localStorage.getItem("weeklyTodos")) || [];
    const storedMonthlyTodos = JSON.parse(localStorage.getItem("monthlyTodos")) || [];

    setTodayTodos(storedTodayTodos);
    setWeeklyTodos(storedWeeklyTodos);
    setMonthlyTodos(storedMonthlyTodos);
  }, []);

  const onDelete = (todo, setTodos, todos) => {
    const updatedTodos = todos.filter((e) => e !== todo);
    setTodos(updatedTodos);
    saveTodosToLocalStorage(window.location.pathname, updatedTodos);
  };

  const onToggle = (todo, setTodos, todos) => {
    const updatedTodos = todos.map((e) => {
      if (e.sno === todo.sno) {
        e.completed = !e.completed;
      }
      return e;
    });
    setTodos(updatedTodos);
  switch (window.location.pathname) {
    case "/":
      localStorage.setItem("todayTodos", JSON.stringify(updatedTodos));
      break;
    case "/weekly":
      localStorage.setItem("weeklyTodos", JSON.stringify(updatedTodos));
      break;
    case "/monthly":
      localStorage.setItem("monthlyTodos", JSON.stringify(updatedTodos));
      break;
    default:
      break;
  }
};
  const addTodo = (title, setTodos, todos) => {
    let sno = todos.length ? todos[todos.length - 1].sno + 1 : 0;
    const myTodo = {
      sno,
      title,
      completed: false
    };
    const updatedTodos = [...todos, myTodo];
    setTodos(updatedTodos);
    saveTodosToLocalStorage(window.location.pathname, updatedTodos);
  };

  const saveTodosToLocalStorage = (pathname, todos) => {
    switch (pathname) {
      case "/":
        localStorage.setItem("todayTodos", JSON.stringify(todos));
        break;
      case "/weekly":
        localStorage.setItem("weeklyTodos", JSON.stringify(todos));
        break;
      case "/monthly":
        localStorage.setItem("monthlyTodos", JSON.stringify(todos));
        break;
      default:
        break;
    }
  };

  return (
    <>
      <Router>
        <Header title="MyTodoList" />
        <div className="main-container">
          <Sidebar />
          <div className="content">
            <Switch>
              <Route exact path="/" render={() => (
                <TodayTasks
                  todos={todayTodos}
                  onDelete={(todo) => onDelete(todo, setTodayTodos, todayTodos)}
                  onToggle={(todo) => onToggle(todo, setTodayTodos, todayTodos)}
                  addTodo={(title) => addTodo(title, setTodayTodos, todayTodos)}
                />
              )} />
              <Route path="/weekly" render={() => (
                <WeeklyTasks
                  todos={weeklyTodos}
                  onDelete={(todo) => onDelete(todo, setWeeklyTodos, weeklyTodos)}
                  onToggle={(todo) => onToggle(todo, setWeeklyTodos, weeklyTodos)}
                  addTodo={(title) => addTodo(title, setWeeklyTodos, weeklyTodos)}
                />
              )} />
              <Route path="/monthly" render={() => (
                <MonthlyTasks
                  todos={monthlyTodos}
                  onDelete={(todo) => onDelete(todo, setMonthlyTodos, monthlyTodos)}
                  onToggle={(todo) => onToggle(todo, setMonthlyTodos, monthlyTodos)}
                  addTodo={(title) => addTodo(title, setMonthlyTodos, monthlyTodos)}
                />
              )} />
              
            </Switch>
          </div>
        </div>
      </Router>
    </>
  );
}

export default App